import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import Dashboard from './Dashboard'; // Add your Dashboard component  
import Header1 from './Header1';
import Sidebar from './Sidebar'; // Sidebar for menu
// import Assignments from './Assignments'; // Sidebar for menu

function app1() {
  return (
    <Router>
      <Sidebar /> {/* Sidebar should be here */}
      <Header1 />
      <Routes>
        <Route path="/" element={<Dashboard />} />
        {/* <Route path="/Assignments" element={<Assignments />} /> */}
      </Routes>
    </Router>
  );
}

export default app1;
