import React, { useState } from 'react';
import { Flex, Box, Button, Input, Stack, Heading, Select } from '@chakra-ui/react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

function Register() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [role, setRole] = useState('');
  const [error, setError] = useState('');

  const navigate = useNavigate();  // For navigation

  const handleRegister = async () => {
    try {
      console.log("Register button clicked");  // Check if this logs on click
      const response = await axios.post('/api/register', {
        username,
        password,
        role,
      });
      if (response.status === 200) {
        // Redirect to login page on success
        navigate(response.data.redirect);  // Redirect to /login
      }
      console.log(response.data);
    } catch (error) {
      console.error('Error during registration', error);
      setError('Registration failed. Try again.');
    }
  };

  return (
    <Flex align="center" justify="center" minHeight="83vh" bg="gray.100">
      <Box p={10} maxWidth="400px" borderWidth={2} borderRadius={20} boxShadow="lg">
        <Heading as="h2" size="lg" textAlign="center" mb={10}>
          Register
        </Heading>
        <Stack spacing={4}>
          <Input 
            placeholder="Username" 
            value={username} 
            onChange={(e) => setUsername(e.target.value)} 
          />
          <Input 
            placeholder="Password" 
            type="password" 
            value={password} 
            onChange={(e) => setPassword(e.target.value)} 
          />
          <Select 
            placeholder="Select Role" 
            value={role} 
            onChange={(e) => setRole(e.target.value)}>
            <option value="student">Student</option>
            <option value="teacher">Teacher</option>
          </Select>

          <Button colorScheme="blue" variant="solid" width="full" onClick={handleRegister}>
            Register
          </Button>
        </Stack>
      </Box>
    </Flex>
  );
}

export default Register;
