import React, { useState } from 'react';
import { Flex, Box, Button, Input, Stack, Heading } from '@chakra-ui/react';
import { useNavigate } from 'react-router-dom';  // For redirection
import axios from 'axios';  // For making HTTP requests

function Login() {
  const [username, setUsername] = useState('');  // State for username
  const [password, setPassword] = useState('');  // State for password
  const [error, setError] = useState('');        // State for error messages
  const navigate = useNavigate();                // Navigation after successful login

  // Handle login form submission
  const handleLogin = async () => {
    try {
      const response = await axios.post('/api/login', {
        username,
        password,
      });

      if (response.status === 200) {
        // On success, you can redirect the user to a dashboard or home page
        navigate('/app1');  // Redirect to dashboard on successful login
      }
    } catch (error) {
      // Handle login error
      console.error('Login failed:', error);
      setError('Invalid username or password');  // Show error message
    }
  };

  return (
    <Flex align="center" justify="center" minHeight="83vh" bg="gray.100">
      <Box p={10} maxWidth="400px" borderWidth={2} borderRadius={20} boxShadow="lg">
        <Heading as="h2" size="lg" textAlign="center" mb={10}>
          Login
        </Heading>
        {error && <p style={{ color: 'red' }}>{error}</p>}
        <Stack spacing={4}>
          <Input
            placeholder="Username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}  // Update username state
          />
          <Input
            placeholder="Password"
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}  // Update password state
          />
          <Button colorScheme="blue" variant="solid" width="full" onClick={handleLogin}>
            Login
          </Button>
        </Stack>
      </Box>
    </Flex>
  );
}

export default Login;
