import React from 'react';
import { Box, Flex, Heading, Link } from '@chakra-ui/react';
import { NavLink } from 'react-router-dom';

const Header = () => {
  return (
    <Box bg="blue.800" p={10}>
      <Flex align="center" justify="space-between">
        <Heading color="white">LMS System</Heading>
        <Flex>
          {/* <Link as={NavLink} to="/" color="white" mx={4}>
            Dashboard
          </Link> */}
          <Link as={NavLink} to="/register" color="white" mx={4}>
            Register
          </Link>
          <Link as={NavLink} to="/login" color="white" mx={4}>
            Login
          </Link>
        </Flex>
      </Flex>
    </Box>
  );
};

export default Header;
